import React from 'react';
import { slide as Menu } from 'react-burger-menu';
import ScrollTo from '../components/ScrollTo';

export default class Nav extends React.Component {
  constructor(props) {
    super(props);
  }

  renderMobileMenu() {
    return (
      <Menu right>
        <ScrollTo
          onClick={() => this.closeMenu()}
          href="overview"
          className="menu-item"
          gtm={{
            event_category: 'Mainsite_Menu',
            event_label: 'Overview'
          }}
        >
          Overview
        </ScrollTo>
        <ScrollTo
          onClick={() => this.closeMenu()}
          href="features"
          className="menu-item"
          gtm={{
            event_category: 'Mainsite_Menu',
            event_label: 'Features'
          }}
        >
          Features
        </ScrollTo>
        <ScrollTo
          onClick={() => this.closeMenu()}
          href="team"
          className="menu-item"
          gtm={{
            event_category: 'Mainsite_Menu',
            event_label: 'Team'
          }}
        >
          Team
        </ScrollTo>
        <style jsx global>{``}</style>
      </Menu>
    );
  }

  render() {
    return <nav>{this.renderMobileMenu()}</nav>;
  }
}
